import { Router } from '@angular/router';
import { SearchActions } from './../../home/reducers/search.actions';
import { getTaxonomies } from './../../product/reducers/selectors';
import { getTotalCartItems } from './../../checkout/reducers/selectors';
import { Component, OnInit, ChangeDetectionStrategy, Input } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState } from '../../interfaces';
import { getAuthStatus } from '../../auth/reducers/selectors';
import { Observable } from 'rxjs/Observable';
import { AuthService } from '../../core/services/auth.service';
import { MenuService } from '../../core/services/menu.service';
import { AuthActions } from '../../auth/actions/auth.actions';
import { UserActions } from '../../user/actions/user.actions';
import { ProductActions } from './../../product/actions/product-actions';
import { MenuActions } from './../actions/menu.actions';
import { DropDown } from './../../core/models/drop_down';
import { getAllDropDowns } from './../reducers/selectors';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeaderComponent implements OnInit {
  isAuthenticated: Observable<boolean>;
  totalCartItems: Observable<number>;
  dropDowns$: Observable<DropDown>;
  @Input() taxonomies: any;
  constructor(
    private store: Store<AppState>,
    private authService: AuthService,
    private authActions: AuthActions,
    private searchActions: SearchActions,
    private userActions: UserActions,
    private menuActions: MenuActions,
    private router: Router,
    private actions: ProductActions,
    private menuSerive: MenuService
  ) {
    this.store.dispatch(this.menuActions.getAllDropDowns());
    this.store.dispatch(this.menuActions.getAllLinkPages());
    this.store.dispatch(this.actions.getAllTaxonomies());
    // this.store.dispatch(this.actions.getAllProducts());
  }

  ngOnInit() {
    this.store.dispatch(this.authActions.authorize());
    this.isAuthenticated = this.store.select(getAuthStatus);
    this.totalCartItems = this.store.select(getTotalCartItems);
    this.dropDowns$ = this.store.select(getAllDropDowns);

    this.isAuthenticated.subscribe(res => {
      if (res == true)
        this.store.dispatch(this.userActions.getUserOrders());
    });
    // [routerLink]="['/blog/', dropDown.name]"


    // this.dropDowns$.subscribe(res => {
    //     console.log(res);
    //   }
    // );
  }

  // navigateByDropDown(name: any){
  //   this.store.dispatch(this.menuActions.getSelectedDropDown(name));
  //   // this.router.navigate(['blog', name]);
  // }

}



