import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { MenuService } from './../../core/services/menu.service';
import { DropDown } from './../../core/models/drop_down';
import { getAllDropDowns } from './../../layout/reducers/selectors';
import { Store } from '@ngrx/store';
import { AppState } from './../../interfaces';
import { environment } from './../../../environments/environment';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-blog-page',
  templateUrl: './blog-page.component.html',
  styleUrls: ['./blog-page.component.scss']
})
export class BlogPageComponent implements OnInit {
  dropDowns$: Observable<DropDown>;
  drop_down_name: string;
  drop_down_item_name: string;
  currentUrl: string;
  dynamicExtraModules = [FormsModule];
  self = this;

  constructor(
    private activatedRoute: ActivatedRoute,
    private menuService: MenuService,
    private store: Store<AppState>,
    private router: Router,
  ) {
    router
    .events
    .filter(e => e instanceof NavigationEnd)
    .subscribe((e: NavigationEnd) => {
      this.currentUrl = e.url;
      this.findCurrentStep(this.currentUrl);
      window.scrollTo(0, 0);
    });
  } 

  ngOnInit() {
    this.drop_down_name = this.activatedRoute.snapshot.params.id;
    this.drop_down_item_name = this.activatedRoute.snapshot.params.item_id;
    this.dropDowns$ = this.store.select(getAllDropDowns);
  }

  onNavigate(str){
    this.router.navigate([str]);
  }

  private findCurrentStep(currentRoute) {
    const currRouteFragments = currentRoute.split('/');
    const length = currRouteFragments.length;
    currRouteFragments.map((route, index) => {
      if(index == 2) this.drop_down_name = route;
      if(index == 3) this.drop_down_item_name = route;
    })
  }
}



