import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { Dentca4Center } from '../models/dentca4-center';

import { PdDentca4CenterService } from './../services/pd-dentca4-center.service';
import { Address } from '../models/address';

@Component({
  selector: 'app-pd-dentca4-map',
  templateUrl: './pd-dentca4-map.component.html',
  styleUrls: ['./pd-dentca4-map.component.scss']
})
export class PdDentca4MapComponent implements OnInit {
  centers: Array<Dentca4Center>;
  positions: Array<string>;

  constructor(
    private centerService: PdDentca4CenterService
  ) {}

  ngOnInit() {
    this.centerService.centersBS.subscribe(centers => {
      this.centers = centers;
      this.positions = centers.map( center => center.address.address1 + ' ' +
                                                      center.address.address2 + ' ' +
                                                      center.address.city     + ' ' +
                                                      center.address.statecode);
    });
  }

  // Map: ready
  onMapReady(event: any) {
    // console.log(event);
  }

  // Map: click
  onMapClick(event: any) {
    // console.log(event);
  }

  // Map: idle
  onIdle(event: any) {
    // console.log(event);
  }

  // Marker: initialized
  onMarkerInit(event: any) {
    // console.log(event);
  }

}



